body {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}

h1, h2, h3, h4 {
    color: purple;
}

.btn-primary,
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):hover,
.btn-primary:not(:disabled):not(.disabled):focus,
.btn-primary:not(:disabled):not(.disabled):visited,
.show>.btn-primary.dropdown-toggle {
    color: #fff;
    background-color: purple;
    border-color: purple;
    width: 200px;
    height: 50px;
    font-size: 22px;
}

.btn-secondary,
.btn-secondary:not(:disabled):not(.disabled):active,
.btn-secondary:not(:disabled):not(.disabled).active,
.btn-secondary:not(:disabled):not(.disabled):hover,
.btn-secondary:not(:disabled):not(.disabled):focus,
.btn-secondary:not(:disabled):not(.disabled):visited,
.show>.btn-secondary.dropdown-toggle {
    color: purple;
    background-color: #ffffff;
    border: 2px solid purple;
    width: 200px;
    height: 50px;
    font-size: 22px;
}

.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled):active:hover,
.btn-primary:not(:disabled):not(.disabled):active:visited,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show>.btn-primary.dropdown-toggle:focus,
.btn-secondary:not(:disabled):not(.disabled):active:focus,
.btn-secondary:not(:disabled):not(.disabled):active:hover,
.btn-secondary:not(:disabled):not(.disabled):active:visited,
.btn-secondary:not(:disabled):not(.disabled).active:focus,
.show>.btn-secondary.dropdown-toggle:focus {
    box-shadow: none;
}

a, a:not([href]):not([tabindex]) {
    color: purple;
}

label {
    color: purple;
    font-size: 18px;
}

.form-control {
    border: 2px solid purple;
}

.clear-fix {
    float: left;
    width: 100%;
}

.btn + .btn {
    margin: 0 10px;
}

@media screen and (max-width: 992px) {
    .container.app-body {
        max-width: 950px;
    }
}

@media screen and (max-width: 767px) {
    .container.app-body {
        max-width: 720px;
    }
    .btn-primary,
    .btn-primary:not(:disabled):not(.disabled):active,
    .btn-primary:not(:disabled):not(.disabled).active,
    .btn-primary:not(:disabled):not(.disabled):hover,
    .btn-primary:not(:disabled):not(.disabled):focus,
    .btn-primary:not(:disabled):not(.disabled):visited,
    .show>.btn-primary.dropdown-toggle,
    .btn-secondary,
    .btn-secondary:not(:disabled):not(.disabled):active,
    .btn-secondary:not(:disabled):not(.disabled).active,
    .btn-secondary:not(:disabled):not(.disabled):hover,
    .btn-secondary:not(:disabled):not(.disabled):focus,
    .btn-secondary:not(:disabled):not(.disabled):visited,
    .show>.btn-secondary.dropdown-toggle {
        width: 90px;
        height: 35px;
        font-size: 14px;
        line-height: 120%;
    }
    .header-logo-section .site-name {
        left: 140px;
        top: 44px;
    }
    .header-logo-section .site-name>span {
        font-size: 36px;
    }
    .header-logo-section .site-name>span+span {
        font-size: 16px;
    }
    .dashboard .nav-tabs .nav-item {
        font-size: 16px;
    }
    .dashboard .nav-tabs .nav-item.active::after {
        border-left: 16px solid transparent;
        border-right: 16px solid transparent;
        border-top: 16px solid purple;
    }
    .user-story-container .story-image-container img {
        height: 100px;
        width: 100px;
    }
    .user-story-container .story-block .read-more-button {
        font-size: 14px;
    }
    .user-story-container .user-image {
        width: 74px;
        height: 74px;
    }
    .user-story-container .user-image img {
        height: 70px;
        width: 70px;
    }
    .tab-content .story-block .read-more-button {
        font-size: 14px;
    }
}

@media screen and (max-width: 576px) {
    .container.app-body {
        max-width: 540px;
        ;
    }
    .container {
        padding: 0 5px;
    }
}